import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {RemittanceProvider} from "../../providers/remittance/remittance";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";


@IonicPage()
@Component({
  selector: 'page-remittance-info02',
  templateUrl: 'remittance-info02.html',
})
export class RemittanceInfo02Page {

  remittanceInfoError02;
  navType;
  exchangeHouseList;
  selectedExchangeHouse;
  pinNo;
  exchangeHouse;
  exchangeHouseID;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public remittanceService: RemittanceProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskToken : MaskTokenProvider) {

    this.navType = this.navParams.get("navType");
    if (this.navType == "1") {
      let list = this.navParams.get("exchangeHouseList");
      // console.log( this.exchangeHouseList);
      // this.exchangeHouseList = JSON.parse(this.exchangeHouseList);
      this.exchangeHouseList = list.agentResponseInfo.exHouseList;
      // console.log(this.exchangeHouseList);
      this.exchangeHouse = "";
      this.exchangeHouseID = "";
      this.pinNo = "";
    }

    if (this.navType == "2") {
      let selExHouse = this.navParams.get("selectedExchangeHouse");
      this.selectedExchangeHouse = JSON.parse(selExHouse);
      let list = this.navParams.get("exchangeHouseList");
      this.exchangeHouseList = JSON.parse(list);
      this.pinNo = this.navParams.get("pinNo");
      this.exchangeHouse = this.selectedExchangeHouse.Title;
      this.exchangeHouseID = this.selectedExchangeHouse.ID;
    }
  }

  //RemittanceInfo01Page is for showing list of exchange house
  selectExchangeHouse = function () {
    this.navCtrl.push('RemittanceInfo01Page', {'pinNo': this.pinNo,'exchangeHouseList': JSON.stringify(this.exchangeHouseList)})
  };

  ShowRemittanceInformation = function (token) {

    this.remittanceInfoError02 = "";
    this.pinNo = token;

    let flag = true;

    if (this.pinNo == "" || typeof(this.pinNo) == "undefined") {
      this.remittanceInfoError02 = "PIN number is required.";
      flag = false;
      return;
    }

    if (flag) {
      let deviceID = this.maskToken.getDeviceID();
      let encDeviceID = this.cryptoUtil.getEncryptVal(deviceID);
      let providedHash = this.cryptoUtil.getSha256EncryptedValue(deviceID + this.exchangeHouseID + this.pinNo.trim() + this.cryptoUtil.salt);
      let encPin = this.cryptoUtil.getEncryptVal(this.pinNo.trim());
      let encExHouse = this.cryptoUtil.getEncryptVal(this.exchangeHouseID);

      this.remittanceService.searchInfoByPinNumber(encDeviceID, encExHouse, encPin, providedHash)
        .then(data => {
          this.remittanceInfoFetchComplete(data);
        });
    }
  };

  remittanceInfoFetchComplete = function (data) {

    this.remittanceInfoError02 = "";
    if (data.status == 0) {
      this.remittanceInfoError02 = "Service unreachable, please try again later.";
    }

    else if (data.statusCode == 404) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else if (data.statusCode == 1) {
      this.pinNo="";


      this.navCtrl.push('RemittanceInfo03Page', {
        'remitSearchResult': JSON.parse(data.object),
        'selectedExchangeHouse': this.exchangeHouse
      });

      this.exchangeHouseID="";
      this.exchangeHouse="";
    }

    else if (data.statusCode == 0) {
      this.remittanceInfoError02 = data.note;
      return;
    }

    else {
      this.remittanceInfoError02 = "System Error, Please Contact with Administrator.";
    }
  };

  ionViewDidLoad() {

  }

}



